import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import React, { useLayoutEffect, useRef } from "react";
import styled from "styled-components";
import Accordion from "../Accordion";

const Section = styled.section`
  min-height: 80vh;
  width: 100vw;
  position: relative;
  color: lavender;
  overflow: hidden;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  ${'' /* border:solid; */}
`;

const Title = styled.h1`
  font-size: ${(props) => props.theme.fontxxl};
  text-transform: uppercase;
  color: ${(props) => props.theme.body};

  margin: 1rem auto;
  border-bottom: 2px solid ${(props) => props.theme.carouselColor};
  width: fit-content;

  @media (max-width: 48em) {
    font-size: ${(props) => props.theme.fontxl};
  }
`;

const Container = styled.div`
  width: 75%;
  margin: 2rem auto;

  display: flex;
  justify-content: space-between;
  align-content: center;

  @media (max-width: 64em) {
    width: 80%;
  }
  @media (max-width: 48em) {
    width: 90%;
    flex-direction: column;

    & > *:last-child {
      & > *:first-child {
        margin-top: 0;
      }
    }
  }
`;

const Box = styled.div`
  width: 45%;
  @media (max-width: 64em) {
    width: 90%;
    align-self: center;
  }
`;

const Faq = () => {
  const ref = useRef(null);
  gsap.registerPlugin(ScrollTrigger);
  useLayoutEffect(() => {
    let element = ref.current;

    ScrollTrigger.create({
      trigger: element,
      start: "bottom bottom",
      end: "bottom top",
      pin: true,
      pinSpacing: false,
      scrub: 1,
      // markers:true,
    });

    return () => {
      ScrollTrigger.kill();
    };
  }, []);

  return (
    <Section ref={ref} id="faq">
      <Title>Faq</Title>

      <Container>
        <Box>
        <Accordion
            ScrollTrigger={ScrollTrigger}
            title="What are the maximum mints per wallet?"
          >
            3 mints per wallet is allowed.
          </Accordion>

         <Accordion
            ScrollTrigger={ScrollTrigger}
            title="Is there a Discord?"
          >
            For the safety reason, we don't have a discord for now. Obviously for the same reason, We haven't displayed our team part.
          </Accordion>

          
          
          <Accordion
            ScrollTrigger={ScrollTrigger}
            title="When will DAO launch?"
          >
            DAO will launch a week after the public sale has completed.
          </Accordion>
         
        </Box>
        <Box>
          
        <Accordion
            ScrollTrigger={ScrollTrigger}
            title="How many series will there be?"
          >
            We will keep on updating according to DAO's voting results.
          </Accordion>
          <Accordion
            ScrollTrigger={ScrollTrigger}
            title="What does our MediaFi mean?"
          >
            We are trying to build a new media model. Compared to the media of web2.0, we do not have a specific platform or community.
Instead of relying on the algorithms to recommend content, we create real content through DAO and make everyone a  reporter, decider even  artist. 
          </Accordion>
          <Accordion
            ScrollTrigger={ScrollTrigger}
            title="Are these all...?"
          >
            Welcome follow us on twitter anytime ,join us with your bravo ideas or web3 techs,just contact us per e-mail or twitter PM.
          </Accordion>
        </Box>
      </Container>
    </Section>
  );
};

export default Faq;
