import React, { useState, useEffect } from "react";
import { ethers, BigNumber } from "ethers";
import { contractAddress, contractABI } from "../utils/constants";

export const Web3Context = React.createContext();

// const { ethereum } = window;

// create instance of contract

const createContract = () => {
  if (window.ethereum) {
    // initialize contract
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const contract = new ethers.Contract(contractAddress, contractABI, signer);
    return contract;
  }
};

export const Web3Provider = ({ children }) => {
  const [currentAccount, setCurrentAccount] = useState([]);
  const [mintAmount, setMintAmount] = useState(1);
  const [totalSupply,setTotalSupply]=useState()

  const contract = createContract();
  useEffect( () => {
    if(!window.ethereum) return


    contract.totalSupply().then((result)=>{
        setTotalSupply(ethers.utils.formatUnits(result, "wei"))
    }).catch('error', console.error);
    //called only once
  },[])
  
  const isConnected = Boolean(currentAccount[0]);

  // create function for Web3 wallet connection
  const connectWallet = async () => {
    try {
      if (window.ethereum) {
        const accounts = await window.ethereum.request({
          method: "eth_requestAccounts",
        });
        setCurrentAccount(accounts[0]);
      }

      let chainId = await window.ethereum.request({
        method: "eth_chainId",
      });
      console.log("Connected to chainId", chainId);
      const MainnetChainId = "0x1";
      if (chainId !== MainnetChainId) {
        alert("Collection is only available on Ethereum Main Network");
        return;
      }
    } catch (error) {
      console.log(error);
      throw new Error("No ethereum object.", error);
    }
  };

  // function to handle mint
  const mintToken = async () => {
    if (isConnected) {
      const MainnetChainId = "0x1";
      let chainId = await window.ethereum.request({
        method: "eth_chainId",
      });
      if (chainId !== MainnetChainId) {
        alert("Careful, collection can only be minted on Ethereum Main Network");
        return;
      }
      try {
        const contract = createContract();

        let num = mintAmount * 0;
        let mintPrice = num.toString();

        const response = await contract.mint(BigNumber.from(mintAmount), {
          value: ethers.utils.parseEther(mintPrice, "ether"),
        });
        console.log("response: ", response);
        window.alert("you've successfully minted");
      } catch (error) {
        console.log(error);
        window.alert(error.reason);
        throw new Error("Error: ", error);
        
      }
    }
  };


  
 




  const checkWalletConnection = async () => {
    try {
      if (isConnected) return console.log("Connected on load up");
    } catch (error) {
      console.log(error);
      throw new Error("No ethereum object:", error);
    }
  };

  const handleDecrement = () => {
    if (mintAmount <= 1) return;
    setMintAmount(mintAmount - 1);
  };

  const handleIncrement = () => {
    if (mintAmount >= 3) return;
    setMintAmount(mintAmount + 1);
  };

  return (
    <Web3Context.Provider
      value={{
        connectWallet,
        currentAccount,
        isConnected,
        mintAmount,
        mintToken,
        setMintAmount,
        handleDecrement,
        handleIncrement,
        checkWalletConnection,
        totalSupply
      }}
    >
      {children}
    </Web3Context.Provider>
  );
};
